import NavLink from '@/components/common/NavLink'
import { isMenuActive } from '@/utils'
import { Drawer, Icon } from '@cetus/ui-kit'
import { Box, Flex, HStack, Image, Text, VStack } from '@chakra-ui/react'
import { useState } from 'react'
import { useLocation } from 'react-router-dom'
import H5Settings from '../settings/h5'
import { ChildMenuItem, IMenusProps, MenuItemWithChildren, MenuItemWithoutChildren } from '../type'

type H5MenuProps = Omit<IMenusProps, 'notifications'> & { isOpen: boolean; onClose: () => void }

const H5Menus = ({ menus, logo, setting, jump, isCetusPump, children, isOpen, onClose }: H5MenuProps) => {
  return (
    <H5MenuDrawer
      menus={menus}
      setting={setting}
      title={<Image src={logo?.image} h="32px" alt="logo" onClick={() => jump(logo?.url)} />}
      isOpen={isOpen}
      onClose={onClose}
      jump={jump}
      isCetusPump={isCetusPump}
    >
      {children}
    </H5MenuDrawer>
  )
}

interface IH5MenuProps extends Omit<IMenusProps, 'notifications' | 'logo' | 'chain'> {
  title: React.ReactNode
  isOpen: boolean
  onClose: () => void
}

const H5MenuDrawer = ({ menus, setting, title, isOpen, onClose, jump, isCetusPump, children }: IH5MenuProps) => {
  const { pathname } = useLocation()
  const more = menus?.find(menu => !!(menu as any)?.socials)
  const [openList, setOpenList] = useState<string[]>([])

  const handleMenuVisible = (title: string) => {
    // 要求:打开新的下拉列表时上一个需要收起
    if (openList.includes(title)) {
      // setOpenList(openList.filter(item => item !== title))
      setOpenList([])
    } else {
      // setOpenList([...openList, title])
      setOpenList([title])
    }
  }

  return (
    <Drawer title={title} isOpen={isOpen} onClose={onClose} placement="left" size="full">
      <VStack w="100%" h="100%" justify="space-between">
        <VStack gap="36px" w="100%" align="flex-start" mt="32px">
          {menus.map(menu => {
            if ((menu as any).children) {
              const active = isMenuActive(menu as any, pathname)
              return (
                <Box key={menu.title} w="100%">
                  <HStack
                    onClick={() => handleMenuVisible(menu.title)}
                    sx={{
                      p: {
                        color: active ? 'text_caption' : 'text_paragraph'
                      },
                      svg: {
                        fill: active ? 'text_caption' : 'text_paragraph'
                      }
                    }}
                    _hover={{
                      p: {
                        color: 'text_caption'
                      },
                      svg: {
                        fill: 'text_caption'
                      }
                    }}
                  >
                    <Text fontSize="16px">{menu.title}</Text>
                    <Icon
                      xlinkHref="#icon-icon_arrow"
                      fontSize="16px"
                      variant="gray"
                      svgFill="primary_gray"
                      transition="transform 0.5s"
                      transform={openList.includes(menu.title) ? 'rotate(180deg)' : 'rotate(0deg)'}
                    />
                  </HStack>
                  {openList.includes(menu.title) && (
                    <VStack
                      mt="12px"
                      bg="bg_secondary"
                      gap="24px"
                      p="20px 16px"
                      w="100%"
                      align="flex-start"
                      border="1px solid"
                      borderColor="border"
                      borderRadius="8px"
                    >
                      {(menu as MenuItemWithChildren).children?.map(item => {
                        const childActive = isMenuActive(item as any, pathname)
                        return (
                          <Item
                            key={item.title}
                            item={{
                              ...item,
                              onClick:
                                item?.type === 'onClick'
                                  ? () => {
                                      item?.onClick?.()
                                      onClose()
                                    }
                                  : undefined
                            }}
                            jump={(path: string) => {
                              jump?.(path)
                              onClose()
                            }}
                            isActive={childActive}
                          />
                        )
                      })}
                    </VStack>
                  )}
                </Box>
              )
            }
            return (
              <NavLink
                key={menu.title}
                type={(menu as MenuItemWithoutChildren)?.type}
                onClick={() => {
                  if ((menu as MenuItemWithoutChildren)?.type === 'onClick') {
                    jump?.((menu as MenuItemWithoutChildren).path!)
                    onClose()
                  }
                }}
                link={(menu as MenuItemWithoutChildren).path!}
                isActive={isMenuActive(menu as MenuItemWithoutChildren, pathname)}
              >
                <Text color="inherit" fontSize="16px" fontWeight="500">
                  {menu.title}
                </Text>
              </NavLink>
            )
          })}
        </VStack>
        <VStack w="100%">
          <H5Settings {...setting} isCetusPump={isCetusPump} />

          <HStack w="100%">
            {more &&
              (more as MenuItemWithChildren)?.socials &&
              (more as MenuItemWithChildren)?.socials?.map(social => {
                return (
                  <Flex
                    key={social.title}
                    flex="1"
                    onClick={() => window.open(social.path)}
                    padding="14px"
                    h="48px"
                    borderRadius="8px"
                    border="1px solid"
                    borderColor="border"
                    bg="menu_item_bg"
                    justify="center"
                    align="center"
                    _hover={{
                      p: {
                        color: 'text_caption'
                      },
                      svg: {
                        fill: 'text_caption'
                      }
                    }}
                  >
                    {social.icon ? (
                      <Box>
                        {typeof social.icon === 'string' ? (
                          <Icon xlinkHref={social.icon} variant="gray" />
                        ) : (
                          <Icon xlinkHref={social.icon.xlinkHref} variant="gray" />
                        )}
                      </Box>
                    ) : null}
                  </Flex>
                )
              })}
          </HStack>
          {children}
        </VStack>
      </VStack>
    </Drawer>
  )
}

interface ItemProps {
  item: ChildMenuItem
  jump: (path: string) => void
  isActive: boolean
}

const Item = ({ item, jump, isActive }: ItemProps) => {
  const [open, setOpen] = useState(false)
  return (
    <>
      <HStack
        key={item.title}
        justify="space-between"
        h="20px"
        lineHeight="20px"
        onClick={() =>
          item?.children && item?.children?.length > 1
            ? setOpen(!open)
            : item?.type === 'onClick'
              ? item?.onClick()
              : item?.type === '_blank'
                ? window.open(item.path, '_blank')
                : jump((item as ChildMenuItem).path!)
        }
        sx={{
          p: {
            color: isActive ? 'text_caption' : 'text_paragraph'
          },
          svg: {
            fill: isActive ? 'text_caption' : 'text_paragraph'
          }
        }}
        _hover={{
          p: {
            color: 'text_caption'
          },
          svg: {
            fill: 'text_caption'
          }
        }}
      >
        <HStack>
          <Icon xlinkHref={(item as ChildMenuItem)?.icon as string} variant="gray" />
          <Box position="relative">
            <Text color="inherit" fontSize="16px" fontWeight="500">
              {item.title}
            </Text>
            {item?.beta && <Image src="/images/icon_beta@2x.png" position="absolute" right="-32px" top="-4px" h="12px" />}
          </Box>
        </HStack>
        {item.children && item.children.length > 0 && (
          <Icon
            xlinkHref="#icon-icon_arrow"
            fontSize="16px"
            variant="gray"
            svgFill="primary_gray"
            transition="transform 0.5s"
            transform={open ? 'rotate(180deg)' : 'rotate(0deg)'}
          />
        )}
      </HStack>
      {open &&
        item.children &&
        item.children.length > 0 &&
        item.children?.map(child => <Item key={child.title} item={child} jump={jump} isActive={false} />)}
    </>
  )
}

export default H5Menus
