import { Modal as BaseModal, Button, ModalBody, ModalCloseButton, ModalContent, ModalFooter, ModalHeader, ModalOverlay } from '@chakra-ui/react'

interface ModalProps {
  title: React.ReactNode
  isOpen: boolean
  onClose(): void
  children: React.ReactNode
  showOverlay?: boolean
  closeable?: boolean
  isCentered?: boolean
  footer?: React.ReactNode
  okText?: string
  cancelText?: string
  onOk?(): void
  onCancel?(): void
}

export default function Modal({
  title = '',
  isOpen,
  showOverlay = true,
  closeable = true,
  isCentered = true,
  onClose,
  children,
  footer,
  okText = 'OK',
  cancelText = 'Cancel',
  onOk,
  onCancel
}: ModalProps) {
  return (
    <BaseModal isOpen={isOpen} onClose={onClose} isCentered={isCentered}>
      {showOverlay && <ModalOverlay />}
      <ModalContent>
        <ModalHeader>{title}</ModalHeader>
        {closeable && <ModalCloseButton />}
        <ModalBody>{children}</ModalBody>
        {footer ? (
          footer
        ) : footer === null ? null : (
          <ModalFooter>
            <Button mr={3} onClick={onCancel} variant="outline">
              {cancelText}
            </Button>
            <Button colorScheme="blue" onClick={onOk}>
              {okText}
            </Button>
          </ModalFooter>
        )}
      </ModalContent>
    </BaseModal>
  )
}
