import useRpcConfigStore from '@cetus/stores/src/useRpcConfigStore'
import { Modal } from '@cetus/ui-kit'
import { useState } from 'react'
import ExplorerContent from '../ExplorerContent'
import H5SettingItem from './Item'

const H5Preferred = () => {
  const { currentExplorer } = useRpcConfigStore()
  const [showOptions, setShowOptions] = useState(false)
  const closeModal = () => {
    setShowOptions(false)
  }
  return (
    <>
      <H5SettingItem
        icon="#icon-icon_preferredexplorer"
        label="Preferred Explorer"
        value={currentExplorer?.name}
        onClick={() => setShowOptions(true)}
      />
      <Modal isOpen={showOptions} title="Preferred Explorer" onClose={() => setShowOptions(false)} footer={null}>
        <ExplorerContent closeModal={closeModal} isH5={true} />
      </Modal>
    </>
  )
}

export default H5Preferred
