import useRpcConfigStore from '@cetus/stores/src/useRpcConfigStore'
import { Modal } from '@cetus/ui-kit'
import { Box } from '@chakra-ui/react'
import { useState } from 'react'
import RPCContent from '../RPCContent'
import H5SettingItem from './Item'

const H5RPCNode = () => {
  const { currentNode } = useRpcConfigStore()
  const [showOptions, setShowOptions] = useState(false)
  const closeModal = () => {
    setShowOptions(false)
  }
  return (
    <>
      <H5SettingItem icon="#icon-icon_rpc" label="RPC Node" value={currentNode.name} onClick={() => setShowOptions(true)} />
      <Modal isOpen={showOptions} title="RPC Node" onClose={() => setShowOptions(false)} footer={null}>
        <Box mb="12px">
          <RPCContent closeModal={closeModal} />
        </Box>
      </Modal>
    </>
  )
}

export default H5RPCNode
